<template>
  <div class="hello v_middle">
    <div class="loader1" v-if="show">
      <i></i><i></i>
    </div>
    <div class="finished" v-if="!show">
      <a href="/#/index"class="enter">Enter</a>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'home',
    data () {
      return {
        show: true
      }
    },
    created () {
      this.cal()
    },
    computed: {
      user: function () {
        return this.$store.getters.getUser
      }
    },
    methods: {
      cal: function () {
        let that = this
        let t = setInterval(function () {
          if (that.user.uid) {
            that.show = false
            clearInterval(t)
          }
        }, 1000)
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
  .hello {
    background: #000;
    position: absolute;
    left: 0;
    top: 0;
    .enter {
      display: inline-block;
      width: 8rem;
      height: 4rem;
      line-height: 4rem;
      font-size: 1.8rem;
      text-align: center;
      border: 1px solid #0fae6f;
    }
  }
  .loader1 {
    margin: 0 auto;
    height: 20px;
    width: 20px;
    position:relative;
    -webkit-animation:spin 1.5s linear infinite;
    -moz-animation:spin 1.5s linear infinite;
  }
  .loader1 i{
    height: 20px;
    width: 20px;
    border-radius: 25px;
    display: block;
    position:absolute;
  }
  .loader1 i:before, .loader1 i:after{
    content:'';
    display:block;
    position:absolute;
    height:inherit;
    width:inherit;
    border-radius: inherit;
  }
  .loader1 i:first-child:before {
    background:rgba(52, 149, 221, 0.9);
    -webkit-animation:rotate-top-left 1.5s linear infinite;
    -moz-animation:rotate-top-left 1.5s linear infinite;
  }
  .loader1 i:first-child:after {
    background:rgba(225, 73, 44, 0.9);
    -webkit-animation:rotate-top-right 1.5s linear infinite;
    -moz-animation:rotate-top-right 1.5s linear infinite;
  }
  .loader1 i:last-child:before {
    background:rgba(249, 206, 43, 0.9);
    -webkit-animation:rotate-bottom-left 1.5s linear infinite;
    -moz-animation:rotate-bottom-left 1.5s linear infinite;
  }
  .loader1 i:last-child:after {
    background:rgba(0, 153, 117, 0.9);
    -webkit-animation:rotate-bottom-right 1.5s linear infinite;
    -moz-animation:rotate-bottom-right 1.5s linear infinite;
  }
  @-webkit-keyframes spin{
    0%{
      -webkit-transform:rotate(0deg);
    }
    50%{
      -webkit-transform:rotate(-180deg);
    }
    100%{
      -webkit-transform:rotate(-360deg);
    }
  }
  @-webkit-keyframes rotate-top-right{
    0%{
      -webkit-transform:rotate(0deg);
    }
    50%{
      -webkit-transform:rotate(-180deg);
      -webkit-transform-origin: 20% 20%;
    }
    100%{
      -webkit-transform:rotate(-360deg);
    }
  }
  @-webkit-keyframes rotate-top-left{
    0%{
      -webkit-transform:rotate(0deg);
    }
    50%{
      -webkit-transform:rotate(180deg);
      -webkit-transform-origin: 80% 20%;
    }
    100%{
      -webkit-transform:rotate(360deg);
    }
  }
  @-webkit-keyframes rotate-bottom-right{
    0%{
      -webkit-transform:rotate(0deg);
    }
    50%{
      -webkit-transform:rotate(-180deg);
      -webkit-transform-origin: 80% 80%;
    }
    100%{
      -webkit-transform:rotate(-360deg);
    }
  }
  @-webkit-keyframes rotate-bottom-left{
    0%{
      -webkit-transform:rotate(0deg);
    }
    50%{
      -webkit-transform:rotate(180deg);
      -webkit-transform-origin: 20% 80%;
    }
    100%{
      -webkit-transform:rotate(360deg);
    }
  }

  @-moz-keyframes spin{
    0%{
      -moz-transform:rotate(0deg);
    }
    50%{
      -moz-transform:rotate(-180deg);
    }
    100%{
      -moz-transform:rotate(-360deg);
    }
  }
  @-moz-keyframes rotate-top-right{
    0%{
      -moz-transform:rotate(0deg);
    }
    50%{
      -moz-transform:rotate(-180deg);
      -moz-transform-origin: 20% 20%;
    }
    100%{
      -moz-transform:rotate(-360deg);
    }
  }
  @-moz-keyframes rotate-top-left{
    0%{
      -moz-transform:rotate(0deg);
    }
    50%{
      -moz-transform:rotate(180deg);
      -moz-transform-origin: 80% 20%;
    }
    100%{
      -moz-transform:rotate(360deg);
    }
  }
  @-moz-keyframes rotate-bottom-right{
    0%{
      -moz-transform:rotate(0deg);
    }
    50%{
      -moz-transform:rotate(-180deg);
      -moz-transform-origin: 80% 80%;
    }
    100%{
      -moz-transform:rotate(-360deg);
    }
  }
  @-moz-keyframes rotate-bottom-left{
    0%{
      -moz-transform:rotate(0deg);
    }
    50%{
      -moz-transform:rotate(180deg);
      -moz-transform-origin: 20% 80%;
    }
    100%{
      -moz-transform:rotate(360deg);
    }
  }
</style>



